<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Создание плана</h2>
                <v-form>
                    <v-text-field
                            label="Название"
                            v-model="name"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            label="Стоимость"
                            type="number"
                            v-model="price"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            label="ID провайдера"
                            v-model="provider_id"
                    ></v-text-field>
                    <v-btn @click="submit" :disabled="!name || !price" class="primary">
                        Создать план
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'

    export default {
        name: "AddPlan",
        data: () => ({
            name: null,
            price: null,
            provider_id: null
        }),
        methods: {
            ...mapActions(['setAlert']),
            submit() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/`, method: 'POST', data: {
                        name: this.name,
                        price: this.price,
                        provider_id: this.provider_id,
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'План создан'
                    })
                    this.$router.push('/plans')
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            }
        }
    }
</script>

